body {
    padding-bottom: 100px;
}
h1 {
    margin-bottom: 50px;
}
.collapse-toggle {
    display: block;
    width: 100%;
    margin-bottom: 15px;
}
corelation-register input, 
local-history-table input {
    width: 100%;
    margin-bottom: 20px;
    padding: 0 8px;
    height: 30px;
    border-radius: 4px;
    box-shadow: none;
    border: 1px solid #ccc;
}
corelation-register .input-group-checkbox, 
local-history-table .input-group-checkbox {
    margin-bottom: 20px;
}
code-input .input-group-checkbox {
    margin-bottom: 20px;
    display: inline-block;
    margin-top: 10px;
}
.buttons-wrapper {
    display: inline-block;
    float: right;
    text-align: center; 
}
corelation-register .input-group-checkbox input,
local-history-table .input-group-checkbox input,
code-input .input-group-checkbox input {
    display: inline-block;
    width: auto;
    margin-left: 20px;
    vertical-align: middle;
    margin-bottom: 5px;
}
.table-condensed {
    width: 100%;
    text-align: center;
    margin-bottom: 0;
    margin-bottom: 40px;    
}
.register-title {
    display:block;
    margin-bottom: 5px;
}
textarea {
    width: 100%;
    display: block;
    resize: none;
    height: 500px;
    padding: 4px 8px;
    line-height: 200%;
    margin-bottom: 20px;
}
.pipeline {
    padding: 10px;
    background: #ededed;
    border: 1px solid #ccc;
}